.sidebar {
    display: none;
    width: 317px;
    text-align: center;
    margin-top: 63px;
    position: absolute;


}

.sidebar.open {
    display: block;
}

.background {
    position: absolute;
    top: 0;
    left: 0;

    height: 100%;
    width: 317px;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
    background: #fff;
    display: flex;
    justify-content: center;
    z-index: 1000;
}

.nav {
    display: flex;
    flex-direction: column;

    position: relative;
    z-index: 1001;

}

.nav a {
    margin-bottom: 45px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 22px;
    color: #000;
    text-decoration: none;
}

.nav a:last-child {
    margin-bottom: 0;
}

.nav a:hover {
    color: #58acff;
}

.nav a.active {
    text-decoration: underline;
    color: #0059b2;
}

.close {
    position: absolute;
    top: -47px;
    right: 24px;
    background-color: #fff;
    border: none;
    z-index: 1000;
}

.close img {
    transform: scale(1.2);
}
