.options {
   display: flex;
   flex-direction: column;
   gap: 10px;
}

.label {
   display: flex;
   align-items: center;
   font-size: 18px;
   cursor: pointer;
}

.radio {
   display: none; /* Прячем стандартный input */
}

.span {
   position: relative;
   padding-left: 30px; /* Отступ для кастомной кнопки */
   cursor: pointer;
}

/* Кастомный кружок */
.span::before {
   content: "";
   position: absolute;
   left: 0;
   top: 50%;
   transform: translateY(-50%);
   width: 15px;
   height: 15px;
   border: 2px solid black;
   border-radius: 50%;
   display: inline-block;
}

/* Внутренний кружок при выборе */
.radio:checked + .span::before {
   background: radial-gradient(black 45%, transparent 50%);
}
