.loader {
    /* Component 5 */

    width: 50px;
    height: 50px;
    border: 5px solid rgba(0, 0, 0, 0.1); /* Светлый цвет */
    border-top-color: #3498db; /* Цвет вращающегося сегмента */
    border-radius: 50%;
    animation: spin 1s linear infinite;

    /*свг и/или анимация*/
}
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}